<script>
  import { setContext as baseSetContext, SvelteComponent } from 'svelte'
  import { fade } from 'svelte/transition'
  export let key = 'simple-modal'
  export let closeButton = true
  export let closeOnEsc = true
  export let closeOnOuterClick = true
  export let roundedCorners = true
  export let styleBg = { top: 0, left: 0 }
  export let styleWindow = {}
  export let styleContent = {}
  export let styleCloseButton = {}
  export let setContext = baseSetContext
  export let transitionBg = fade
  export let transitionBgProps = { duration: 150 }
  export let transitionWindow = transitionBg
  export let transitionWindowProps = transitionBgProps

  const defaultState = {
    closeButton,
    closeOnEsc,
    closeOnOuterClick,
    roundedCorners,
    styleBg,
    styleWindow,
    styleContent,
    styleCloseButton,
    transitionBg,
    transitionBgProps,
    transitionWindow,
    transitionWindowProps,
  }
  let state = { ...defaultState }
  let Component = null
  let props = null
  let background
  let wrap
  let modalWindow
  const camelCaseToDash = (str) => str.replace(/([a-zA-Z])(?=[A-Z])/g, '$1-').toLowerCase()
  const toCssString = (props) =>
    Object.keys(props).reduce((str, key) => `${str}; ${camelCaseToDash(key)}: ${props[key]}`, '')
  // eslint-disable-next-line no-prototype-builtins
  const isSvelteComponent = (component) => SvelteComponent.isPrototypeOf(component)

  $: cssBg = toCssString(state.styleBg)
  $: cssWindow = toCssString(state.styleWindow)
  $: cssContent = toCssString(state.styleContent)
  $: cssCloseButton = toCssString(state.styleCloseButton)
  $: currentTransitionBg = state.transitionBg
  $: currentTransitionWindow = state.transitionWindow

  const toVoid = () => {}

  let onOpen = toVoid
  let onClose = toVoid
  let onOpened = toVoid
  let onClosed = toVoid

  const open = (NewComponent, newProps = {}, options = {}, callback = {}) => {
    Component = NewComponent
    props = newProps
    state = { ...defaultState, ...options }
    onOpen = callback.onOpen || toVoid
    onClose = callback.onClose || toVoid
    onOpened = callback.onOpened || toVoid
    onClosed = callback.onClosed || toVoid
  }

  const close = (callback = {}) => {
    onClose = callback.onClose || onClose
    onClosed = callback.onClosed || onClosed
    Component = null
    props = null
  }

  const handleKeydown = (event) => {
    if (state.closeOnEsc && Component && event.key === 'Escape') {
      event.preventDefault()
      close()
    }
    if (Component && event.key === 'Tab') {
      // trap focus
      const nodes = modalWindow.querySelectorAll('*')
      const tabbable = Array.from(nodes).filter((node) => node.tabIndex >= 0)
      let index = tabbable.indexOf(document.activeElement)
      if (index === -1 && event.shiftKey) index = 0
      index += tabbable.length + (event.shiftKey ? -1 : 1)
      index %= tabbable.length
      tabbable[index].focus()
      event.preventDefault()
    }
  }
  const handleOuterClick = (event) => {
    if (state.closeOnOuterClick && (event.target === background || event.target === wrap)) {
      event.preventDefault()
      close()
    }
  }
  setContext(key, { open, close })
</script>

<svelte:window on:keydown={handleKeydown} />

{#if Component}
  <div
    class="bg"
    on:click={handleOuterClick}
    bind:this={background}
    transition:currentTransitionBg={state.transitionBgProps}
    style={cssBg}>
    <div class="window-wrap" bind:this={wrap}>
      <div
        class="window"
        class:rounded-corners={state.roundedCorners}
        role="dialog"
        aria-modal="true"
        bind:this={modalWindow}
        transition:currentTransitionWindow={state.transitionWindowProps}
        on:introstart={onOpen}
        on:outrostart={onClose}
        on:introend={onOpened}
        on:outroend={onClosed}
        style={cssWindow}>
        {#if state.closeButton}
          {#if isSvelteComponent(state.closeButton)}
            <svelte:component this={state.closeButton} onClose={close} />
          {:else}<button on:click={close} class="close" style={cssCloseButton} />{/if}
        {/if}
        <div class="content" class:rounded-corners={state.roundedCorners} style={cssContent}>
          <svelte:component this={Component} {...props} />
        </div>
      </div>
    </div>
  </div>
{/if}
<slot />

<style>
  * {
    box-sizing: border-box;
  }
  .bg {
    position: fixed;
    z-index: 1000;
    display: flex;
    flex-direction: column;
    justify-content: center;
    width: 100vw;
    height: 100vh;
    background: rgba(0, 0, 0, 0.9);
  }
  .window-wrap {
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
    margin: 2rem;
    max-height: 100%;
  }
  .window {
    position: relative;
    /* width: 100px; */
    max-width: 100%;
    max-height: 100%;
    margin: 2rem auto;
    color: black;
    background: white;
  }
  .content {
    position: relative;
    max-height: calc(100vh - 4rem);
    overflow: auto;
  }

  .rounded-corners {
    border-radius: 40px 0 40px 0;
  }

  .close {
    display: block;
    box-sizing: border-box;
    position: absolute;
    z-index: 1000;
    top: -30px;
    left: -30px;
    margin: 0;
    padding: 0;
    width: 30px;
    height: 30px;
    border: 0;
    color: black;
    border-radius: 50%;
    background: #cecece;
    transition: transform 0.15s cubic-bezier(0.25, 0.1, 0.25, 1),
      background 0.15s cubic-bezier(0.25, 0.1, 0.25, 1);
    -webkit-appearance: none;
  }
  .close:before,
  .close:after {
    content: '';
    display: block;
    box-sizing: border-box;
    position: absolute;
    top: 50%;
    left: 50%;
    /* margin-left: -9px;
    margin-top: -1px; */
    width: 18px;
    height: 2px;
    border-radius: 1px;
    background: gray;
    transform-origin: center;
    transition: height 0.15s cubic-bezier(0.25, 0.1, 0.25, 1),
      background 0.15s cubic-bezier(0.25, 0.1, 0.25, 1);
  }
  .close:before {
    transform: translate(-50%, -50%) rotate(45deg);
  }
  .close:after {
    transform: translate(-50%, -50%) rotate(-45deg);
  }
  .close:hover {
    background: white;
  }
  /* .close:hover:before,
  .close:hover:after {
    background: white;
  } */
  /* .close:focus {
    border-color: #3399ff;
    box-shadow: 0 0 0 2px #3399ff;
  } */
  .close:active {
    transform: scale(0.9);
  }
  .close:hover,
  .close:focus,
  .close:active {
    outline: none;
  }</style>
